<template>
  <ListContainer
    :title="`${$t('category_picture')} ${type}`"
    @onClick="handleToMore('/video')"
  >
    <PictureList :dataSource="fakePictures" />
  </ListContainer>
</template>
<script>
import ListContainer from "@/components/ListContainer";
import PictureList from "@/components/PictureList";
import { pictures } from "../../fakeData";
import mobileMixin from "../../mixin/mobileMixin";

const fakePictures = pictures.slice(0, 20).map((item) => ({
  ...item,
  path: "/picture",
}));

export default {
  mixins: [mobileMixin],
  components: {
    ListContainer,
    PictureList,
  },
  data() {
    return {
      fakePictures,
    };
  },
  computed: {
    type() {
      return this.$route.params.type || "";
    },
  },
  watch: {
    isMobile: {
      immediate: true,
      handler(newValue, oldValue) {
        if (newValue && !oldValue) {
          this.handleAddAds();
        } else {
          this.handleRemoveAds();
        }
      },
    },
  },
  methods: {
    handleAddAds() {
      this.fakePictures = this.addAds(this.fakePictures, 4);
    },
    handleRemoveAds() {
      this.fakePictures = this.removeAds(this.fakePictures);
    },
  },
};
</script>
